.page {
  font-family: "Playfair Display", serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    background: $white;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

}

.page__body--with-menu {
  overflow: hidden;
}
